/*------------------------------------*\
    layout
\*------------------------------------*/

.landing {
    background-color: rgb(250,250,250);
    overflow: auto;


    p {
        font-size: 17px;
        margin-top: 15px;
        line-height: 1.4;
        color: rgba(black,.6);

    }

    a {
        @include cool-underline(gray, gray, white, 95%);
        font-weight: 400;
    }


}

.landing__wrapper {
    max-width: 1200px;

    margin: 50px auto 120px auto;
    padding: 0 50px;
}


.landing__row {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-top: 80px;
}


.landing__main-title {
    font-weight: 700;
    //font-family: "Karla";
    margin-top: 120px;
    font-size: 50px;
    color: rgba(black,.75);
}

.landing__title {
    font-weight: 700;
    //font-family: "Karla";
    margin-top: 0;
    font-size: 22px;
    color: rgba(black,.75);
}


.landing__section {
    flex: 1;
    padding-right: 80px;
}




/*------------------------------------*\
    media-queries
\*------------------------------------*/
@media screen and (max-width: 1000px) {

    .landing__row {
        flex-direction: column;

    }

    .landing__title {
        margin-top: 50px;
    }

}

